.body {
  min-width: 300px;
  margin: auto;
  height: 100%;
  line-height: 1;
  padding-top: 20px;
  background: -webkit-linear-gradient(#4cda63 0%, #4cda63 30%, #ffffff 30%, #eaf0f5 31%, #ffffff 100%);
}
* {
  box-sizing: border-box;
}
.cil {
  top: 20px;
  left: 50%;
  margin-left: -40px;
  position: absolute;
  width: 80px;
  height: 80px;
  line-height: 80px;
  border-radius: 80px;
  text-align: center;
  color: #333;
  background-color: #fff;
  font-size: 16px;
}
h2 {
  font-size: 14px;
  text-align: center;
  color: #999999;
}
.canvas {
  position: relative;
  text-align: center;
  margin: 10px;
}
.box {
  background-color: #fff;
  margin: 20px 10px;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
ul,
li {
  padding: 0;
}
ul {
  display: flex;
  flex-wrap: wrap;
}
ul li {
  width: 100%;
  list-style: none;
  font-size: 12px;
}
ul li span {
  margin-left: 10px;
}
ul li > a {
  display: flex;
  margin: 10px;
  padding: 10px;
  align-items: center;
  text-align: left;
  background-color: #fff;
  border-radius: 5px;
  color: #666666;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
}
ul li > span {
  display: block;
}
ul li i {
  font-size: 32px;
  color: #4cda63 !important;
}
